@import url(https://fonts.googleapis.com/css?family=Poppins:300,400,500,600,700);
.App {
  display: -webkit-flex;
  display: flex;
  width: 100%;
  -webkit-align-items: stretch;
          align-items: stretch;
  overflow-y: hidden;
  margin: 0 0 0 0;
}

body {
  font-family: 'Poppins', sans-serif;
  background: #fafafa;
}

.content {
  padding: 20px;
  margin-left: 0;
  height: 100vh;
}
#content_container {
  width: 100%;
  height: 85%;
  overflow-y: scroll;
}
@media only screen and (max-width: 500px) {
  body {
    overflow: hidden;
  }

  .content.is-open {
    margin-left: 100%;
  }

  .sidebar.is-open {
    min-width: 100%;
    max-width: 100%;
    margin-left: 0;
    transition: all 0.5s, height 0s;
  }

  .sidebar.is-open > .sidebar-header span {
    display: unset;
  }

  li a.dropdown-toggle::after {
    display: inline-block;
    position: relative;
    left: 68%;
  }
}


table {
  width: 100%;
  margin-bottom: 8px;
  text-align: center;
}
.table th {
  line-height: 60px;
}
.table td {
  line-height: 70px;
}
.table td,
.table th {
  padding: 0 16px;
  border-top: none;
  border-bottom: 1px solid gainsboro;
}

.product-page {
  margin: 50px 20px;
  overflow-x: scroll;
}
.product__img {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  width: 70px;
  height: 70px;
}

.login_wrapper {
  right: 0px;
  margin: 0px auto;
  margin-top: 5%;
  max-width: 350px;
  position: relative;
}

.login_form {
  z-index: 22;
}

.registration_form, .login_form {
  position: absolute;
  top: 0px;
  width: 100%;
}

.animate {
  -webkit-animation-duration: 0.5s;
  -webkit-animation-timing-function: ease;
  -webkit-animation-fill-mode: both;
  -moz-animation-duration: 0.5s;
  -moz-animation-timing-function: ease;
  -moz-animation-fill-mode: both;
  -o-animation-duration: 0.5s;
  -o-animation-timing-function: ease;
  -o-animation-fill-mode: both;
  -ms-animation-duration: 0.5s;
  -ms-animation-timing-function: ease;
  -ms-animation-fill-mode: both;
  animation-duration: 0.5s;
  animation-timing-function: ease;
  animation-fill-mode: both;
}
.login {
  background: #F7F7F7;
}
.login_content {
  margin: 0 auto;
  padding: 25px 0 0;
  position: relative;
  text-align: center;
  text-shadow: 0 1px 0 #fff;
  min-width: 280px;
}

*, *:before, *:after {
  box-sizing: border-box;
}
*, *:before, *:after {
  box-sizing: border-box;
}

.login_content h1 {
  font: normal 25px Helvetica, Arial, sans-serif;
  letter-spacing: -0.05em;
  line-height: 20px;
  margin: 10px 0 30px;
}

.separator {
  border-top: 1px solid #D8D8D8;
  margin-top: 10px;
  padding-top: 10px;
}

.login_content h1:before {
  background: #7e7e7e;
  background: linear-gradient(to left, #7e7e7e 0%, #fff 100%);
  left: 0;
}
.reset_pass {
  color:black;
}
.signup-form {
  padding: 50px 0;
  text-align: center;
}
.signup-form__row {
  -webkit-justify-content: center;
          justify-content: center;
}
.form-content {
  text-align: left;
}
.form-btn {
  text-align: center;
  padding: 10px 15px 0px 15px;
}
.form-btn__login {
  color: darkgray;
  font-size: 14px;
}
.form-btn__login:hover {
  color: gray;
  text-decoration: none;
}


.nav-link {
  color: white;
}
.nav-link:link {
  color: white;
}
.nav-link:visited {
  color: white;
}
.nav-link:hover {
  color: #7386d5;
}
.nav-link:active {
  color: white;
}
.sidebar {
  min-width: 250px;
  max-width: 250px;
  background: #7386d5;
  color: #fff;
  margin-left: -250px;
  transition: all 0.5s;
}

.sidebar.is-open {
  margin-left: 0;
  transition: 0.5s;
}

.sidebar-header {
  background: #6d7fcc;
}

.sidebar-header h3 {
  color: #fff;
  padding: 1em;
}

.sidebar ul p {
  color: #fff;
  padding: 10px;
}

.menu-open {
  background: #6d7fcc;
}

.nav-item:hover {
  color: #7386d5;
  background: #fff;
}

.items-menu {
  color: #fff;
  background: #6d7fcc;
}

li a.dropdown-toggle::after {
  display: -webkit-inline-flex;
  display: inline-flex;
  position: relative;
  left: 60%;
  top: 10%;
}

.sidebar-header > span {
  position: relative;
  float: right;
  margin: 0.5em;
  font-size: 2rem;
  cursor: pointer;
  display: none;
}
.side-menu {
  height: calc(100vh - 130px);
  overflow-y: scroll;
}

.side-menu::-webkit-scrollbar {
  width: 10px;
}

.side-menu::-webkit-scrollbar-thumb {
  background: #5466b3;
  border-radius: 3px;
}

.side-menu::-webkit-scrollbar-thumb:hover {
  background: #3a4c99;
}

a.dropdown-toggle {
  color: white;
}

