table {
    width: 50%;
    margin-bottom: 8px;
    text-align: center;
  }
  .table th {
    line-height: 60px;
  }
  .table td {
    line-height: 70px;
  }
  .table td,
  .table th {
    padding: 0 16px;
    border-top: none;
    border-bottom: 1px solid gainsboro;
  }
  
  .product-page {
    margin: 50px 20px;
  }
  .product__img {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 70px;
    height: 70px;
  }
  